<template>
  <div id="order_record">
    <div class="tit">
      <span data-v-6940696e="">订单交易记录</span>
    </div>
    <div class="record_money">
      <div class="tot_m">
        <p>{{Total.total_money}}</p> <span>总提交金额</span>
      </div>
      <div class="succ_m">
        <p>{{Total.total_success_money}}</p> <span>成功金额</span>
      </div>
      <div class="succ_m user_m">
        <p>{{Total.total_profit}}</p><span>商户进账</span>
      </div>
    </div>
    <div class="record_table">
      <DatePicker type="daterange" :value="time" split-panels :options="options" placeholder="请选择订单创建时间"
        style="width: 240px" @on-change="datechange($event)"></DatePicker>
      <div class="select_box select_box_none">
        <img src="/static/icon/4.png" style="width: 13px; margin-top: -7px; z-index:10;">
        <Select style="width:160px;position: absolute;z-index:10;top:0px" v-model="field">
          <Option v-for="item in select.field_select" :value="item.value" :key="item.value">{{item.title}}</Option>
        </Select>
        <Input placeholder="请输入对应的关键词" style="width:380px;position: relative;z-index:8;" v-model="val" />
      </div>
      <div data-v-6940696e="" class="search_some" @click.stop="ajaxlist(1,time,field,val)">查询</div>
    </div>
    <div class="store_bottom">
      <table data-v-9154b70e="">
        <thead data-v-9154b70e="">
          <tr data-v-9154b70e="" style="background: rgba(244,247,254,.25);">
            <th data-v-9154b70e="">创建时间</th>
            <th data-v-9154b70e="">平台订单号</th>
            <th data-v-9154b70e="">商户订单号</th>
            <th data-v-9154b70e="">支付方式</th>
            <th data-v-9154b70e="">订单金额</th>
            <th data-v-9154b70e="">实付金额</th>
            <th data-v-9154b70e="">通知状态</th>
            <th data-v-9154b70e="">支付状态</th>
            <th data-v-9154b70e="">订单备注</th>
            <th data-v-9154b70e="">详情</th>
          </tr>
        </thead>
        <tbody data-v-9154b70e="">
          <td v-if="list.length == 0" colspan="10" align="center">
            <div class="none_now">
              <img src="/static/icon/7.png">
              <div>暂无相关数据</div>
            </div>
          </td>
          <tr data-v-9154b70e="" v-if="list.length >= 1" v-for="(item,index) in list" :key="item.Id">
            <td data-v-9154b70e="">{{item.sub_time}}</td>
            <td data-v-9154b70e="" style="color: rgb(56, 108, 250); cursor: pointer;" @click="changeDrawer(index)">
              <span data-v-9154b70e="">{{item.apiorder_id}}</span>
            </td>
            <td data-v-9154b70e="">{{item.order_id}}</span></td>
            <td data-v-9154b70e="">{{item.type}}</td>
            <td data-v-9154b70e="">{{item.order_money}}</td>
            <td data-v-9154b70e="">{{item.order_price}}</td>
            <td data-v-9154b70e="" v-if="item.notice == 2">
              <Icon type="md-checkmark-circle" />
            </td>
            <td data-v-9154b70e="" v-if="item.notice == 1">
              <Icon type="ios-alert" />
            </td>
            <td data-v-9154b70e="" v-if="item.notice == 3">
              <Icon type="md-close-circle" />
            </td>
            <td data-v-9154b70e="" v-if="item.status == 2">
              <Icon type="md-checkmark-circle" />
            </td>
            <td data-v-9154b70e="" v-if="item.status == 1">
              <Icon type="ios-alert" />
            </td>
            <td data-v-9154b70e="" v-if="item.status == 3">
              <Icon type="md-close-circle" />
            </td>
            <td data-v-9154b70e="">{{item.remarks}}</td>
            <td data-v-9154b70e="" style="color: rgb(56, 108, 250); user-select: none; cursor: pointer; font-weight: bold;"
              @click="changeDrawer(index)">详情</td>
          </tr>
        </tbody>
      </table>
      <Page :total="page.total" :page-size="15" :current="page.current" show-total class="fenye" v-if="list.length>=1"
        @on-change="ajaxlist($event,time,field,val)" />
    </div>
    <Drawer :closable="false" v-model="drawer_status" width="406px" @on-close="drawer=''">
      <div data-v-131c0b29 class="left_show">
        <div data-v-131c0b29="" class="tite">详情信息</div>
        <div data-v-131c0b29="" class="text_box">
          <span data-v-131c0b29="">创建时间</span>
          <span data-v-131c0b29="">{{drawer.time}}</span>
        </div>
        <div data-v-131c0b29="" class="text_box">
          <span data-v-131c0b29="">平台订单号</span>
          <span data-v-131c0b29="">{{drawer.apiorder_id}}</span>
        </div>
        <div data-v-131c0b29="" class="text_box">
          <span data-v-131c0b29="">商户订单号</span>
          <span data-v-131c0b29="">{{drawer.order_id}}</span>
        </div>
        <div data-v-131c0b29="" class="text_box">
          <span data-v-131c0b29="">支付方式</span>
          <span data-v-131c0b29="">{{drawer.type}}</span>
        </div>
        <div data-v-131c0b29="" class="text_box">
          <span data-v-131c0b29="">订单金额</span>
          <span data-v-131c0b29="">{{drawer.order_money}}</span>
        </div>
        <div data-v-131c0b29="" class="text_box">
          <span data-v-131c0b29="">实付金额</span>
          <span data-v-131c0b29="">{{drawer.order_price}}</span>
        </div>
        <div data-v-131c0b29="" class="text_box">
          <span data-v-131c0b29="">订单手续费</span>
          <span data-v-131c0b29="">{{drawer.order_service}}</span>
        </div>
        <div data-v-131c0b29="" class="text_box">
          <span data-v-131c0b29="">支付通道手续费</span>
          <span data-v-131c0b29="">{{drawer.channel_service}}</span>
        </div>
        <div data-v-131c0b29="" class="text_box">
          <span data-v-131c0b29="">同步通知地址</span>
          <span data-v-131c0b29="">{{drawer.return_url}}</span>
        </div>
        <div data-v-131c0b29="" class="text_box">
          <span data-v-131c0b29="">异步通知地址</span>
          <span data-v-131c0b29="">{{drawer.notify_url}}</span>
        </div>
        <div data-v-131c0b29="" class="text_box">
          <span data-v-131c0b29="">通知状态</span>
          <span data-v-131c0b29="" v-if="drawer.notice == 2">通知成功</span>
          <span data-v-131c0b29="" v-if="drawer.notice == 3">通知失败</span>
          <span data-v-131c0b29="" v-if="drawer.notice == 1">等待付款</span>
        </div>
        <div data-v-131c0b29="" class="text_box">
          <span data-v-131c0b29="">支付状态</span>
          <span data-v-131c0b29="" v-if="drawer.status == 2">支付成功</span>
          <span data-v-131c0b29="" v-if="drawer.status == 3">订单过期</span>
          <span data-v-131c0b29="" v-if="drawer.status == 1">等待付款</span>
        </div>
        <div data-v-131c0b29="" class="text_box">
          <span data-v-131c0b29="">订单备注</span>
          <span data-v-131c0b29="">{{drawer.remarks}}</span>
        </div>
        <div data-v-131c0b29="" class="text_box zhuihou">
          <span data-v-131c0b29="">实际进账</span>
          <span data-v-131c0b29="" style="color: rgb(56, 108, 250);">{{drawer.profit}}元</span>
        </div>
      </div>
    </Drawer>
    <Spin fix v-if="spin"></Spin>
  </div>
</template>
<script>
  export default {
    data() {
      return {
        select: {
          field_select: [{
              value: "0",
              title: '全部订单'
            },
            {
              value: 'order_id',
              title: '商户订单号'
            },
            {
              value: 'apiorder_id',
              title: '平台订单号'
            }
          ],
        },
        field:"0",
        val:"",
        time: '',
        drawer: '',
        drawer_status:false,
        list:[],
        Total:"",
        page: {
          current: 1,
          total: ''
        },
        spin: true,
        options: {
          disabledDate(date) {
            return date && date.valueOf() - 86400000 > Date.now();
          }
        }
      }
    },
    mounted() {
      this.time = [this.getDay(0),this.getDay(1)];
      this.ajaxlist(1,this.time,this.field,this.val);
    },
    methods: {
      getDay(num) {
          var today = new Date();
          var nowTime = today.getTime();
          var ms = 24*3600*1000*num;
          today.setTime(parseInt(nowTime + ms));
          var oYear = today.getFullYear();
          var oMoth = (today.getMonth() + 1).toString();
          if (oMoth.length <= 1) oMoth = '0' + oMoth;
          var oDay = today.getDate().toString();
          if (oDay.length <= 1) oDay = '0' + oDay;
          return oYear+'-'+oMoth+'-'+oDay;
      },
      ajaxlist(page, time,field,val) {
        this.spin = true;
        this.$http.post('api/user/order_record',{page:page,time:time,field:field,val:val}, res => {
              console.log(res);
              this.spin = false;
             if (res.status == 200) {
                 if(res.data.code == 200){
                  this.list = res.data.data.list;
                  this.Total = res.data.data.Total;
                  this.page.current = res.data.data.current_page;
                  this.page.total = res.data.data.total;
                 }else if(res.data.code == 201){
                   this.$Message.warning(res.data.msg);
                 }
             } else {
                console.log(res.message);
             }
        });
      },
      datechange(n) {
        this.time = n;
        this.ajaxlist(1,this.time,this.field, this.val);
      },
      changeDrawer(e) {
        this.drawer = JSON.parse(JSON.stringify(this.list[e]));
        if (this.drawer.status == 2) {
            this.drawer.profit = parseFloat(this.drawer.order_price) - parseFloat(this.drawer.order_service) - parseFloat(
            this.drawer.channel_service).toFixed(2);
        } else {
          this.drawer.profit = 0;
        }
        this.drawer_status = true;
      }
    }
  }
</script>



